* {
  box-sizing: border-box;
}

body {
  background-color: #fafafa;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  line-height: 1.88;
}

p {
  margin: 0;
}

input {
  font: inherit;
  border: none;
  outline: none;
  padding: 24px;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
  padding: 8px 12px;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

.search-form_wrap {
  width: 100%;
  z-index: 9;
  background: linear-gradient(90deg, #e3e6e5, #03ada2);
  margin: 0 auto;
  padding: 16px 0;
  position: fixed;
  top: 0;
  left: 0;
}

.search-form {
  margin: 0 auto;
  display: flex;
}

.search_input {
  height: 50px;
  background-color: rgba(47, 83, 79, .16);
  border: 1px solid #838383;
  border-right: none;
  border-radius: 8px 0 0 8px;
  padding-left: 16px;
}

.search_btn {
  height: 50px;
  width: 50px;
  background-color: rgba(47, 83, 79, .16);
  border: 1px solid #838383;
  border-left: none;
  border-radius: 0 8px 8px 0;
  justify-content: center;
  align-items: center;
  display: flex;
}

.search_btn_icon {
  fill: #838383;
}

.wrap {
  justify-content: center;
  align-items: center;
  display: flex;
}

.gallery {
  flex-wrap: wrap;
  gap: 16px;
  padding: 0 0 0 14px;
  display: flex;
}

.photo-card {
  width: calc(33.3333% - 15px);
  position: relative;
}

.photo-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 16px;
}

.info-list {
  color: #fafafa;
  background-color: rgba(17, 93, 154, .35);
  border-radius: 0 0 16px 16px;
  justify-content: space-between;
  margin: 0;
  padding: 8px;
  list-style: none;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

